@import url(https://use.fontawesome.com/d0c316cf02.js);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.12.1/css/all.min.css);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&family=Montserrat:wght@500&family=Roboto+Mono:ital,wght@1,200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0}.main{display:flex;flex-direction:column;grid-gap:10px;gap:10px;height:100vh;align-items:center;justify-content:center;font-family:"Noto Sans JP";letter-spacing:-1px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.main .container{display:flex;flex-direction:column;width:500px;text-align:center;border-radius:8px;padding:20px;background-color:rgba(255,255,255,.6);box-shadow:0 0 1rem 0 rgba(0,0,0,.2);-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px)}.main .container .title{font-size:24px;width:1fr;padding:5px}.main .container .stats{width:1fr;padding:5px;margin-bottom:10px;font-size:14px}.main .container .stats .emoji{margin-left:3px}.main .container #timer-label{width:1fr;padding:5px;font-size:24px}.main .container #timer-label .fa-fire{margin-left:7px;color:orange}.main .container #time-left{width:1fr;padding:5px;margin-top:20px;margin-bottom:20px;font-size:45px;font-family:Roboto Mono;letter-spacing:-5px}.main .container .control-div{display:flex;flex-direction:column;width:1fr;align-items:center}.main .container .control-div .top-control-div{display:flex;flex-direction:column;width:100%;align-items:center}.main .container .control-div .top-control-div #start_stop{width:40%;background-color:#fff}.main .container .control-div .top-control-div #start_stop:hover{transform:scale(1.05)}.main .container .control-div .bottom-control-div{display:flex;width:60%;flex-direction:row;margin:5px;padding:5px;width:1fr;grid-gap:20px;gap:20px}.main .container .control-div .bottom-control-div #settings{flex:1 1;background-color:#000;color:#fff}.main .container .control-div .bottom-control-div #reset{flex:1 1;background-color:#000;color:#fff}.main .container .control-div .bottom-control-div #settings:hover,.main .container .control-div .bottom-control-div #reset:hover{background-color:#272727}.main .settings-overlay{display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:8px;padding:20px 20px 5px 20px;background-color:rgba(255,255,255,.6);box-shadow:0 0 1rem 0 rgba(0,0,0,.2);-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px)}.main .settings-overlay .settings-div{display:grid;grid-gap:8px;gap:8px;text-align:right;padding-bottom:15px;grid-template-column:4fr 2fr 1fr 1fr;grid-template-row:1fr 1fr;grid-template-areas:"focus-label focus-length focus-inc focus-dec" "rest-label rest-length rest-inc rest-dec"}.main .settings-overlay .settings-div #session-label{grid-area:focus-label}.main .settings-overlay .settings-div #session-length{grid-area:focus-length}.main .settings-overlay .settings-div #session-increment{grid-area:focus-inc}.main .settings-overlay .settings-div #session-decrement{grid-area:focus-dec}.main .settings-overlay .settings-div #break-label{grid-area:rest-label}.main .settings-overlay .settings-div #break-length{grid-area:rest-length}.main .settings-overlay .settings-div #break-increment{grid-area:rest-inc}.main .settings-overlay .settings-div #break-decrement{grid-area:rest-dec}.main .settings-overlay #close-button{margin-bottom:10px;background-color:#000;color:#fff}.main .settings-overlay #close-button:hover{background-color:#272727}.button{background-color:#fff;border-radius:4px;margin:5px;padding:7px;padding-left:15px;padding-right:15px;font-size:14px}.adjust-button{text-align:center;background-color:#fff;border-radius:4px;padding:2px 5px 3px 4px;margin:3px;font-size:10px}#session-increment:hover,#break-increment:hover{background-color:#ff8989}#session-decrement:hover,#break-decrement:hover{background-color:#b1ffb1}.bg-color-red{background-color:#ff8989}.bg-color-green{background-color:#b1ffb1}.credit{margin-top:20px;padding-bottom:20px;text-align:center;font-family:Montserrat;letter-spacing:0px;font-size:10px}.credit a:link,.credit a:visited{color:#000}
